import React from 'react'
import '../../App.css'
import {Field, Form} from "react-final-form";

const NewTask = (props) => {

    const onSubmit = (value) => {
        if (value.newTask) props.saveTaskOnLS(value.newTask)
        value.newTask = ""
    }

    return (
        <div>
            <Form
                onSubmit={onSubmit}
                render={(props) => (
                    <form onSubmit={props.handleSubmit}>
                        <label className="input__label">New Task</label>
                        <Field
                            name='newTask'
                            component="input"
                            type="text"
                            className="input__task"
                        />
                        <div className="container__button">
                            <button className="btn btn__add">ADD</button>
                        </div>
                    </form>
                )}
            />
        </div>
    )
}

export default NewTask