import React from 'react'
import star from '../../image/star_border.svg'
import del from '../../image/Delete.png'
import '../../App.css'
import classNames from "classnames";

const Tasks = (props) => {

    const onClick = (e, type, id) => {
        // eslint-disable-next-line default-case
        switch (type) {
            case 'import':
                props.toggleTaskImportant(id)
                break
            case 'done':
                props.toggleTaskDone(id)
                break
            case 'delete':
                props.deleteTask(id)
                break
        }
    }

    return (
        <div className="task__list">
            {props.tasks.map(task =>
                <div className='task__container' key={task.id}>
                    <div className={classNames('task', {'done': task.isDone})}
                         onClick={(e) => onClick(e, 'done', task.id - 1)}>
                        <img src={star}
                             className={classNames('star', {'hidden': !task.isImportant})}
                             alt='звезда важной задачи'/>
                        <span className={classNames('task__text', {'strike': task.isDone}, {'task_important': task.isImportant})}>
                            {task.value}
                        </span>
                    </div>
                    <button className={classNames('btn', 'btn__important', 'hidden', {'btn__important_noActive': task.isImportant})}
                            onClick={(e) => onClick(e, 'import', task.id - 1)}>
                        {task.isImportant? 'NOT IMPORTANT':'MARK IMPORTANT'}
                    </button>
                    <button className={classNames('btn', 'btn__delete', 'hidden')}
                    onClick={(e) => onClick(e, 'delete', task.id)}>
                        <img src={del} alt='кнопка удалить'/>
                    </button>
                </div>)
            }
        </div>
    )
}

export default Tasks