import React from 'react'
import classNames from "classnames";
import '../../App.css'

const Filter = (props) => {

    return (
        <div className="sort">
            <Button type='All' {...props}/>
            <Button type='Active' {...props}/>
            <Button type='Done' {...props}/>
        </div>
    )
}

const Button = (props) => {

    const onCLick = (type) => {
        props.changeFilter(type)
    }

    return <button className={classNames('btn', 'sort__item', {'active_sort': props.filter === props.type})} onClick={() => onCLick(props.type)}>
        <p>{props.type}</p>
    </button>
}

export default Filter