import {connect} from "react-redux";
import Tasks from "./Tasks";
import {deleteTask, extractTasksFromLS, toggleTaskDone, toggleTaskImportant} from "../../Redux/tasksReducer";
import {getTasksActive, getTasksDone, getTaskSearch} from "../../Redux/selectors";
import React from "react";

class FilterTask extends React.Component {

    componentDidMount()
    {
        this.props.extractTasksFromLS()
    }

    chooseFilter = (filter) => {
        switch (filter) {
            case 'All':
                return this.props.tasks
            case 'Active':
                return this.props.activeTask
            case 'Done':
                return this.props.doneTask
            default:
                return
        }
    }

    render() {

        return <Tasks toggleTaskDone={this.props.toggleTaskDone}
                      toggleTaskImportant={this.props.toggleTaskImportant}
                      deleteTask={this.props.deleteTask}
                      tasks={this.chooseFilter(this.props.filter)}/>
    }
}

const mapStateToProps = (state) => {
    return {
        filter: state.tasks.filter,
        tasks: getTaskSearch(state),
        doneTask: getTasksDone(state),
        activeTask: getTasksActive(state)
    }
}

export default connect(mapStateToProps, {extractTasksFromLS, toggleTaskDone, toggleTaskImportant, deleteTask})(FilterTask)