const ADD_TASKS = 'ADD_TASKS'
const CHANGE_FILTER = 'CHANGE_FILTER'
const SET_SEARCH = 'SET_SEARCH'

let initialScale = {
    tasks: [],
    filter: 'All',
    search: ''
}

const tasksReducers = (state = initialScale, action) => {
    switch (action.type) {
        case ADD_TASKS:
            return {...state, tasks: action.sessionMass}
        case CHANGE_FILTER:
            return {...state, filter: action.filter}
        case SET_SEARCH:
            return {...state, search: action.search}
        default:
            return state
    }
}

const addTasksToState = (sessionMass) => ({type: ADD_TASKS, sessionMass})
export const changeFilter = (filter) => ({type:CHANGE_FILTER, filter})
export const setSearch = (search) => ({type:SET_SEARCH, search})

const sessionFromLS = () => JSON.parse(localStorage.getItem('session'))

const sendStateToLS = (dispatch, sessionMass) => {
    localStorage.session = JSON.stringify(sessionMass)
    sessionMass = JSON.parse(localStorage.getItem('session'))
    dispatch(addTasksToState(sessionMass))
}


export const saveTaskOnLS = (text) => {
    return (dispatch, state) => {
        const id = state().tasks.tasks.length + 1
        const sessionMass = sessionFromLS() || []
        sessionMass.push({"id": id, "value": text, "isDone" : false, "isImportant" : false})
        sendStateToLS(dispatch, sessionMass)
    }
}

export const extractTasksFromLS = () => {
    return (dispatch, state) => {
        if (localStorage.getItem('session') !== JSON.stringify(state().tasks.tasks)) {
            const sessionMass = sessionFromLS() || []
            dispatch(addTasksToState(sessionMass))
        }
    }
}

export const toggleTaskDone = (id) => {
    return (dispatch) => {
        let sessionMass = sessionFromLS()
        sessionMass[id].isDone? sessionMass[id].isDone = false : sessionMass[id].isDone = true;
        sendStateToLS(dispatch, sessionMass)
    }
}

export const toggleTaskImportant = (id) => {
    return (dispatch) => {
        let sessionMass = sessionFromLS()
        sessionMass[id].isImportant? sessionMass[id].isImportant = false : sessionMass[id].isImportant = true;
        sendStateToLS(dispatch, sessionMass)
    }
}

export const deleteTask = (id) => {
    return (dispatch) => {
        let sessionMass = sessionFromLS()
        sessionMass.splice(sessionMass.findIndex(item => item.id === id), 1);															// находим в массиве сессии по id объект соответствующий элементу списка и удаляем его
        for (let index = id; index <= sessionMass.length; index++) {																			// перебираем массив, чтобы поправить id оставшихся объектов (чтоб шли по порядку)
            sessionMass[index-1].id = index;
        }
        sendStateToLS(dispatch, sessionMass)
    }
}

export default tasksReducers