import React from 'react'
import '../../App.css';
import cross from '../../image/cross.png'

const Search = (props) => {

    const clear = () => {
        props.setSearch('')
    }

    return (
        <div className="header__search_wrap">
            <form onSubmit={(event) => event.preventDefault()}>
                <input
                    type="text"
                    name="search"
                    className="header__search"
                    placeholder="Search task for to do"
                    onChange={(event) => props.setSearch(event.target.value)}
                    value={props.search}/>
            </form>
            <button className='search__btn' onClick={clear}><img src={cross} alt='Очистить' className='btn__cross'/></button>
        </div>
    )
}

export default Search